.tetris {
  align-items: center;
  cursor: default;
  display: flex;
  flex-direction: column;
  height: calc(100vh - #{var(--header-height)} - #{var(--footer-height)});
  justify-content: center;
  outline: 0;
  text-decoration: none;
}

.score {
  font-size: 24px;
  font-weight: bold;
}
